  padding: 10px;
  border-radius: 15px;

  .grilla-suma {
    display: grid;
    grid-template-columns: 1fr 20px 1fr 20px 1fr 20px 1fr;
    /* max-width: 900px; */
    align-items: center;
    gap: 30px;

    .acumulados,
    .nulos,
    .no-registradas,
    .total {
      p {
        color: #060606;
      }
    }

    .total {
      big {
        font-size: 2rem;
      }
      big,
      .porcentaje {
        // color: #d2127e;
        color: #582D73;

      }
    }
  }
}

.table-responsive {
  thead {
    tr {
      background-color: #582D73;
      color: #fff;

      th {
        vertical-align: middle;
        img {
          max-height: 35px;
        }
      }
    }
  }
  tbody {
    tr {
      td {
        vertical-align: middle;
        img {
          max-width: 50px;
        }
      }
      .votos {
        text-align: center;
        p {
          font-size: 2rem !important;
          font-weight: bold !important;
        }
      }
    }
  }
}

.boton-verdetalle {
  background-color: #582D73;
  border-color: transparent;
}

.contenedor {
  .rowActas {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;

    .elemento {
      /* max-width: 140px; */
      display: inline-block;
      margin-right: 30px;
      border: 1px solid black;
      padding: 0;
      img {
        max-width: 40px;
        padding-bottom: 20px;
        padding-top: 20px;
      }

      .grilla-partidos {
        display: grid;
        background-color: #f4f4f4;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
        padding: 10px;
        align-items: flex-end;
      }
    }

    /* @media (min-width: 1200px) {
      max-width: 1000px;
    }
    @media (min-width: 1400px) {
      max-width: 1200px;
    } */
  }
}

.porcentaje-participacion-ciudadana {
  position: absolute;
  top: 9px;
  left: 35px;
  color: #fff;
}

.progress {
  background-color: #f4f4f4;
  height: 70px !important;
}
</style>
